.content-box{
    color: var(--color-mavi);
    width: 100%;
    height: auto;
    min-height: 100vh;
    padding: 2em 16%;
    .content-content-box{
        background-color: var(--color-gri);
        color: var(--color-siyah);
        box-sizing: border-box;
        width: 68%;
        height: auto;
        padding: 2em 1em;
        border-radius: 0 0 1em 1em;
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(282px, 1fr));
        gap: 1em;
    }
}
@media (min-width:768px) and (max-width:992px){
    .content-box{
        padding: 2em 8%;
        .content-content-box{
            width: 84%;
        }
    }
}
@media (max-width:768px){
    .content-box{
        padding: 2em 5%; //16
        .content-content-box{
            width: 90%; //68
            grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
        }
    }
}
